<template lang="html">
    <div>
        <el-table
        ref="multipleTable"
        :data="tableData3"
        border
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          label="车辆信息"
          width="400"
          prop="car">

        </el-table-column>
        <el-table-column
          prop="name"
          label="司机信息"
          width="400">
        </el-table-column>
        <el-table-column
          prop="address"
          label="定位地址"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item>
          <el-input placeholder="请输入要分享的企业代码和公司名称"></el-input>
        </el-form-item>
        <el-form-item>
          <router-link to="/WDCD/share"><el-button type="primary">确认分享</el-button></router-link>
        </el-form-item>
      </el-form>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        tableData3: [{
          car: '沪DG5060 厢式车 9.6米 10吨 30放',
          name:'衣龙魁',
          address: '山东省烟台市莱阳市G204（龙门西路）'
        }, {
          car: '沪DG5060 厢式车 9.6米 10吨 30放',
          name:'衣龙魁',
          address: '山东省烟台市莱阳市G204（龙门西路）'
        }, {
          car: '沪DG5060 厢式车 9.6米 10吨 30放',
          name:'衣龙魁',
          address: '山东省烟台市莱阳市G204（龙门西路）'
        }],
        multipleSelection: []
      }
    },

    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    }
  }
</script>
<style lang="less">
  .el-table__row{
    height: 100px;
  }
  .el-form{
    margin-top:20px;
  }
  .el-input__inner{
    width: 320px;
  }
  .el-button--primary{
    width:120px;
    background: #0E9A0A;
  }

</style>



